@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,800);


body {
    margin: 0;
    font-family: 'Avenir';
  }
  
  .sidebar {
    margin: 0;
    padding: 0;
    width: 80px;
    background-color: #f1f1f1;
    position: fixed;
    height: 100%;
    overflow: auto;
  }
  
  .sidebar a {
    display: block;
    color: black;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-top: 20px;
    text-decoration: none;
  }
   
  .sidebar a:hover {
    color: rgb(8, 62, 240);
  }
  
  div.content {
    margin-left: 200px;
    padding: 1px 16px;
    height: 1000px;
  }

  .bot{
      position: absolute;
      bottom: 0;
      left: 30px;
      font-size: 20px;
  }

  .sidebar .bot a{
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }

  .sidebar .side-btn{
    display: none;
  }

  .sidebar #side-check{
    display: none;
  }
  
  @media screen and (max-width: 700px) {
    .sidebar {
      width: 100%;
      height: auto;
      position: fixed;
      z-index: 5;
      text-align: center;
    }
    .sidebar .side-links a {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      margin-top: 50px;
    }
    div.content {margin-left: 0;}

    .sidebar .side-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
      background-color: rgba(0, 0, 0, 0.3);
    }
    .sidebar .side-btn label {
      display: inline-block;
      width: 50px;
      height: 50px;
      padding: 13px;
    }
    .sidebar .side-btn label span {
      display: block;
      width: 25px;
      height: 10px;
      border-top: 2px solid #eee;
    }
    .sidebar #side-check:not(:checked) + .side-links {
      height: 0px;
    }
    .sidebar #side-check:checked + .side-links {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
    .sidebar .bot a{
      display: none;
  }
    
  }
  
  @media screen and (max-width: 400px) {
    .sidebar a {
      text-align: center;
      float: none;
    }
  }

  .sidebar a{
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }



  .loader-page {
    background-color: #333;
    position: fixed;
    width: 100vw;
    height: 100%;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
  }
  .loader-container {
    /* border: 1px solid steelblue; */
    width: 600px;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .loader {
    width: 500px;
    height: 5px;
    background-color: rgba(0,0,0,0.3);
    position: relative;
  }

  .loader-counter {
    position: absolute;
    font-size: 200px;
    font-family: 'Lato' ;
    font-weight:bold;
    color: steelblue;
    z-index: 9;
  }
  h1 {
    text-align: center;
    color: white;
    font-size: 3rem;
  }
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  h1 {
    position: absolute;
    z-index: 1;
  }

@media (max-width: 768px) {
h1 {
  font-size: 5vw;
}
}

.brk-img-double-wide{
  position:relative;
  padding-bottom:120px;
  z-index:0
}
.brk-img-double-wide__container{
  width:80%
}
.brk-img-double-wide__container:first-child{

  overflow:hidden;
  opacity:.5;
  -webkit-transition:opacity .3s,-webkit-transform .3s;
  transition:opacity .3s,-webkit-transform .3s;
  transition:opacity .3s,transform .3s;
  transition:opacity .3s,transform .3s,-webkit-transform .3s;
  border-top-right-radius:25px;
  border-top-left-radius:25px;
}
.brk-bordered-theme .brk-img-double-wide__container:first-child{
  border-top-right-radius:var(--b-radius);
  border-top-left-radius:var(--b-radius);
}
.brk-img-double-wide__container:last-child{
  position:absolute;
  right:0;
  top:120px;
  overflow:hidden;
  opacity:1;
  -webkit-transition:opacity .3s,-webkit-transform .3s;
  transition:opacity .3s,-webkit-transform .3s;
  transition:opacity .3s,transform .3s;
  transition:opacity .3s,transform .3s,-webkit-transform .3s;
  border-bottom-left-radius:25px
  
}
.brk-bordered-theme .brk-img-double-wide__container:last-child{
  border-bottom-left-radius:var(--b-radius)
}
.brk-img-double-wide:hover .brk-img-double-wide__container:first-child{
  z-index:1;
  opacity:1;
  -webkit-transform:translate(30px,30px);
  transform:translate(30px,30px)
}
.brk-img-double-wide:hover .brk-img-double-wide__container:last-child{
  z-index:-1;
  opacity:.5;
  -webkit-transform:translate(-30px,-30px);
  transform:translate(-30px,-30px)
}
.lazyload{
  border-bottom-left-radius:25px;
  width:60%;
  -webkit-transform: translate(-80px);
          transform: translate(-80px)
}

@media screen and (max-width: 480px) {
  .brk-img-double-wide:hover .brk-img-double-wide__container:first-child{
  z-index:1;
  opacity:1;
  -webkit-transform:translate(10px,10px);
  transform:translate(10px,10px)
}
.brk-img-double-wide:hover .brk-img-double-wide__container:last-child{
  z-index:-1;
  opacity:.5;
  -webkit-transform:translate(-20px,-10px);
  transform:translate(-20px,-10px)
}
.lazyload{
  border-bottom-left-radius:25px;
  width:100%;
  margin-left: 10%;
  border-radius: 25px;
  -webkit-transform: translate(0px,0px);
          transform: translate(0px,0px);
}.container{
  margin-left: 0%;
  padding-top: 50px;
}
}




