/* PROGRESS */
.progress {
  background-color: #e5e9eb;
  height: 0.25em;
  position: relative;
  width: 24em;
}
.progress-bar {
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-name: width;
          animation-name: width;
  background-image: -webkit-linear-gradient(left, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
  background-image: linear-gradient(to right, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
  background-size: 24em 0.25em;
  height: 100%;
  position: relative;
  width: 40%;
}
.progress-shadow {
  background-image: -webkit-linear-gradient(top, #eaecee, transparent);
  background-image: linear-gradient(to bottom, #eaecee, transparent);
  height: 4em;
  position: absolute;
  top: 100%;
  -webkit-transform: skew(45deg);
          transform: skew(45deg);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  width: 100%;
}

/* ANIMATIONS */
@-webkit-keyframes width {
  0%, 100% {
    -webkit-transition-timing-function: cubic-bezier(1, 0, 0.65, 0.85);
            transition-timing-function: cubic-bezier(1, 0, 0.65, 0.85);
  }
  0% {
    width: 0;
  }
  100% {
    width: 40%;
  }
}
@keyframes width {
  0%, 100% {
    -webkit-transition-timing-function: cubic-bezier(1, 0, 0.65, 0.85);
            transition-timing-function: cubic-bezier(1, 0, 0.65, 0.85);
  }
  0% {
    width: 0;
  }
  100% {
    width: 40%;
  }
}

